  height: 48px;
  display: flex;
  background: var(--color-surface-elevated);
  transition: transform 0.3s ease, background 0.3s ease;
  align-items: center;
  border-radius: var(--border-radius-full);
  justify-content: center;
}
 
.showcase-steps-visual {
  flex: 1;
  overflow: hidden;
  position: relative;
  box-shadow: 0 8px 24px color-mix(in srgb, var(--color-primary) 12%, transparent);
  border-radius: var(--border-radius-lg);
}
 
.showcase-steps-content {
  flex: 1;
}
 
.showcase-steps-indicator {
  width: 48px;
  border: none;
  cursor: pointer;
  height: 4px;
  background: var(--color-border);
  transition: background 0.3s ease;
  border-radius: var(--border-radius-full);
}
 
.showcase-steps-image {
  width: 100%;
  height: 480px;
  display: block;
  object-fit: cover;
}
 
.showcase-steps-number {
  top: var(--spacing-xl);
  left: var(--spacing-xl);
  color: var(--color-surface);
  width: 80px;
  border: 2px solid var(--color-surface);
  height: 80px;
  display: flex;
  position: absolute;
  font-size: var(--font-size-3xl);
  background: var(--color-backplate);
  align-items: center;
  font-family: var(--font-family-heading);
  font-weight: var(--font-weight-heading);
  border-radius: var(--border-radius-full);
  backdrop-filter: blur(10px);
  justify-content: center;
}
 
.showcase-steps-title {
  color: var(--color-on-surface);
  font-size: var(--font-size-3xl);
  font-family: var(--font-family-heading);
  font-weight: var(--font-weight-heading);
  margin-bottom: var(--spacing-md);
}
 
.showcase-steps-description {
  color: var(--color-on-surface-secondary);
  font-size: var(--font-size-lg);
  font-family: var(--font-family-body);
  line-height: 1.6;
  margin-bottom: var(--spacing-xl);
}
 
.showcase-steps-features {
  gap: var(--spacing-md);
  display: flex;
  flex-direction: column;
}
 
.showcase-steps-feature {
  gap: var(--spacing-md);
  color: var(--color-on-surface);
  display: flex;
  font-size: var(--font-size-base);
  align-items: center;
  font-family: var(--font-family-body);
}
 
.stats-indicators {
  padding: var(--spacing-4xl) 0;
  background: var(--color-surface-elevated);
}
 
.stats-indicators-container {
  margin: 0 auto;
  padding: 0 var(--spacing-xl);
  max-width: var(--content-max-width);
}
 
.stats-indicators-header {
  text-align: center;
  margin-bottom: var(--spacing-3xl);
}
 
.stats-indicators-grid {
  gap: var(--spacing-xl);
