  flex: 1;
  display: flex;
  padding: var(--spacing-3xl);
  flex-direction: column;
  justify-content: center;
}
 
.cta-submit-visual {
  flex: 1;
  position: relative;
  min-height: 400px;
}
 
.cta-submit-actions {
  gap: var(--spacing-md);
