  font-family: var(--font-family-body);
}
 
.cta-submit-image {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  object-fit: cover;
}
 
.cta-submit-button {
  gap: var(--spacing-sm);
  color: var(--color-on-surface);
  display: flex;
  background: var(--color-surface);
  align-items: center;
}
 
.hero-carousel-slide.active {
  display: block;
}
 
.hero-carousel-slide  .hero-title {
  color: var(--color-surface);
  margin-bottom: var(--spacing-md);
}
 
.hero-carousel-slide  .hero-subtitle {
  color: var(--color-surface);
  opacity: 0.9;
  margin-bottom: var(--spacing-2xl);
}
 
.hero-carousel-input-wrapper  svg {
  left: var(--spacing-md);
  color: var(--color-on-surface-secondary);
  position: absolute;
  pointer-events: none;
}
 
.hero-carousel-input:focus {
  box-shadow: 0 0 0 3px color-mix(in srgb, var(--color-primary) 10%, transparent);
  border-color: var(--color-primary);
}
 
.hero-carousel-prev:hover {
  transform: scale(1.1);
  background: color-mix(in srgb, var(--color-backplate) 80%, var(--color-surface));
}
 
.hero-carousel-next:hover {
  transform: scale(1.1);
  background: color-mix(in srgb, var(--color-backplate) 80%, var(--color-surface));
}
 
.hero-carousel-dot.active {
  transform: scale(1.2);
  background: var(--color-surface);
}
 
.search-filters-header  .section-title {
  margin-bottom: var(--spacing-sm);
}
 
.search-filters-select:focus {
  border-color: var(--color-primary);
}
 
.search-filters-input:focus {
  border-color: var(--color-primary);
}
 
.search-filters-price-input:focus {
  border-color: var(--color-primary);
}
 

 
.product-listings-header  .section-title {
  margin-bottom: var(--spacing-sm);
}
 
