  flex-shrink: 0;
}
 

 
.features-benefits-header  .section-title {
  margin-bottom: var(--spacing-sm);
}
 
.features-benefits-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 16px 32px color-mix(in srgb, var(--color-primary) 10%, transparent);
}
 
.showcase-steps-header  .section-title {
  margin-bottom: var(--spacing-sm);
}
 
.showcase-steps-slide.active {
  gap: var(--spacing-3xl);
  display: flex;
  align-items: center;
}
 
.showcase-steps-feature  svg {
  color: var(--color-primary);
  flex-shrink: 0;
}
 
.showcase-steps-prev:hover {
  color: var(--color-on-primary);
  transform: scale(1.1);
  background: var(--color-primary);
  border-color: var(--color-primary);
}
 
.showcase-steps-next:hover {
  color: var(--color-on-primary);
  transform: scale(1.1);
  background: var(--color-primary);
  border-color: var(--color-primary);
}
 
.showcase-steps-indicator.active {
  background: var(--color-primary);
}
 
.stats-indicators-header  .section-title {
  margin-bottom: var(--spacing-sm);
}
 
.stats-indicators-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 8px 16px color-mix(in srgb, var(--color-primary) 8%, transparent);
}
 
.cta-submit-content  .section-title {
  color: var(--color-on-primary);
  margin-bottom: var(--spacing-md);
}
 
.cta-submit-content  .section-subtitle {
  color: var(--color-on-primary);
  opacity: 0.95;
  margin-bottom: var(--spacing-2xl);
}
 
.cta-submit-button:hover {
  transform: translateY(-2px);
  background: var(--color-surface-elevated);
}
 
.cta-submit-actions  .btn-outline {
  color: var(--color-on-primary);
  border-color: var(--color-on-primary);
}
 
.cta-submit-actions  .btn-outline:hover {
  color: var(--color-primary);
  background: var(--color-on-primary);
}
 

 
.Headings {
  font-size: 2em;
  font-family: Inter;
  font-weight: 700;
  line-height: 1.2;
  letter-spacing: 0.02em;
}
 
.BodyFont {
  font-size: 1rem;
  font-family: Roboto;
  font-weight: 400;
  line-height: 1.6;
  letter-spacing: 0.03em;
}
 
.Content {
  font-size: 16px;
  font-family: Inter;
  font-weight: 400;
  line-height: 1.15;
  text-transform: none;
  text-decoration: none;
}
 
